<template>
  <div class="addressList">
    <nav-head title="收货地址" FixedOrTransparency="isFixed NoTransparency"/>
    <div>
      <div class="address-list">
        <div>
          <address-item
            v-for="item in AddressList"
            :item="item"
            :key="item.id"
            @handler="deleteButtonHandler"
          />
        </div>
        <no-address v-show="AddressList.length===0"></no-address>
      </div>
      <div class="bottom">
        <router-link to="addNewAddress" tag="a" class="btn-add">添加新地址</router-link>
      </div>
    </div>
    <v-dialog v-show="this.display" @close="onCloseDialog">
      <div class="text">确认删除地址信息？</div>
      <div class="btn-group">
        <mt-button @click.native="onCloseDialog" class="btn-cancel">取消</mt-button>
        <mt-button @click.native="deleteAddress" class="btn-confirm">确定</mt-button>
      </div>
    </v-dialog>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import AddressItem from '../components/AddressItem.vue'
import NoAddress from '../components/NoAddress.vue'
import VDialog from '../components/VDialog.vue'
import { Button, CellSwipe, Toast } from 'mint-ui'
import { mapActions, mapState } from 'vuex'
import { GET_ADDRESS_LIST } from '../../../../vuex/Mine'
import http from '../../../../tool/http'

export default {
  name: 'addressList',
  data: function () {
    return {
      display: false,
      addressId: ''
    }
  },
  methods: {
    onCloseDialog () {
      this.display = false
    },
    deleteButtonHandler (val) {
      this.addressId = val
      this.display = true
    },
    deleteAddress () {
      if (this.addressId) {
        http.postDeleteAddress({ id: this.addressId }).then(data => {
          if (data) {
            this.display = false
          } else {
            Toast('删除地址失败')
          }
        })
      }
    },
    ...mapActions({
      getAddressList: GET_ADDRESS_LIST
    })
  },
  components: {
    NavHead,
    [Button.name]: Button,
    [CellSwipe.name]: CellSwipe,
    AddressItem,
    NoAddress,
    VDialog
  },
  computed: {
    ...mapState({
      AddressList: state => state.mine.AddressList
    })
  },
  created () {
    this.getAddressList()
  }
}
</script>
<style scoped lang="scss">
  div.addressList {
    position:relative;
    z-index: 3;
    min-height: 100vh;
    @if $IF365 { background-color: $background-light-color; }
    @else { background-color: #F5F5F5; }
    text-align: center;
    div.address-list {
      padding-bottom: 70px;
    }
    div.bottom{
      position: fixed;
      width: 100%;
      padding: 0;
      bottom: 0;
      left: 0;
      .btn-add {
        display: block;
        text-align: center;
        border: none;
        background-color: $background-light-color;
        color: $back-color;
        border-radius: 0;
        height: 55px;
        line-height: 55px;
        font-size: 17px;
        text-decoration: none;
      }
    }
  }
  .text {
    font-size: 16px;
    color: #333;
    font-weight: bold;
    line-height: 26px;
    margin: 15px 0 25px 0;
  }
  .btn-cancel{
    background: #DDD ;
    width: 40% ;
    height: 40px;
    color: #999;
    margin: 0 15px 0 0 ;
    font-size: 16px;
  }
  .btn-confirm{
    background-color: $back-color;
    @if $IF365 { color: #131313; }
    @else { color: white; }
    width: 40% ;
    height: 40px;
    font-size: 16px;
  }
</style>
