<template>
  <div class="item-wrap text-left margin-top-15">
    <mt-cell-swipe
      :right="rightOptions" class="item">
      <div slot="title">
        <p class="address-title">
          <template v-if="item.is_default === 1">
            <span class="badge">默认</span>
          </template>
          {{item.province_name}} {{item.city_name}} {{item.county_name}}
        </p>
        <p class="address-detail">{{item.address}}<br><span class="color-light margin-right-10">{{item.user_name}}</span>{{item.user_phone}}<br><span class="color-light margin-right-10">QQ号码</span>{{item.user_qq}}</p>
        <router-link :to="{name:'editAddress', query:{list:item}}" tag="button" class="btn-edit">编辑</router-link>
      </div>
    </mt-cell-swipe>
  </div>
</template>
<script>
import { CellSwipe, Badge } from 'mint-ui'

export default {
  name: 'addressItem',
  props: {
    item: { type: Object },
    display: { type: Boolean }
  },
  data: function () {
    return {
      rightOptions: [
        {
          content: '删除',
          style: { background: '#F64646', color: '#fff', width: '60px', padding: '0', textAlign: 'center', lineHeight: '118px', fontSize: '15px' },
          handler: () => this.deleteHandler(this.item.id)
        }]
    }
  },
  methods: {
    showCancelBind () {
      this.display = true
    },
    closeCancelBind () {
      this.display = false
    },
    deleteHandler (val) {
      this.$emit('handler', val)
    }
  },
  components: {
    [CellSwipe.name]: CellSwipe,
    [Badge.name]: Badge
  }
}
</script>
<style scoped lang="scss">
  .item-wrap {
    @if $IF365 { background-color: #434343; }
    @else { background-color: white; }
    .item {
      @if $IF365 { background-color: #434343; }
      @else { background-color: white; }
      text-align: left;
    }
  }
  .address-title {
    font-size: 16px;
    line-height: 26px;
    color: $font-color;
    font-weight: bold;
    margin-top: 10px;
    padding-left: 15px;
  }
  .address-detail {
    @if $IF365 { color: #CCC; }
    @else { color: white; }
    font-size: 14px;
    line-height: 24px;
    padding-left: 15px;
    padding-bottom: 10px;
  }
  .btn-edit {
    position: absolute;
    right: 0;
    top: 0;
    color: $back-color;
    @if $IF365 { background-color: #434343; border-left: 1px solid #999; }
    @else { background-color: white; border-left: 1px solid #eee; }
    height: 90px;
    border: none;
    box-shadow: none;
    font-size: 15px;
    margin-top: 14px;
    width: 60px;
    padding: 0;
  }
  .margin-right-10 {
    margin-right: 10px;
  }
  .margin-top-15 {
    margin-top: 15px;
  }
  .color-light{
    @if $IF365 { color: #888; }
    @else { color: #999; }
  }
  .badge {
    display: inline-block;
    height: 20px;
    line-height: 20px;
    border-radius: 4px;
    color: $back-color;
    @if $IF365 { background-color: white; }
    @else { background-color: #CCC; }
    font-size: 14px;
    padding: 0 4px;
    margin-right: 5px;
  }
</style>
